/**
 * Ê®ôÊ∫ñÂèÉÊï∏Êé•Âè£
 */
export interface StandardParams {
    project: string;
    language: SupportedLocale;
}

/**
 * ÂæûË´ãÊ±Ç‰∏≠ÊèêÂèñÊ®ôÊ∫ñÂèÉÊï∏
 */
export function extractStandardParams(request: NextRequest): StandardParams {
    const { searchParams } = new URL(request.url);

    return {
        // ÂÑ™ÂÖà‰ΩøÁî® URL ÂèÉÊï∏ÔºåÊ≤íÊúâÁöÑË©±‰ΩøÁî®Áµ±‰∏ÄÈÖçÁΩÆÁöÑÈ†êË®≠ÂÄº
        project: searchParams.get('project') || APP_CONFIG.PROJECT_NAME,
        language: (searchParams.get('language') as SupportedLocale) || LOCALE_CONFIG.DEFAULT_LOCALE,
    };
}

/**
 * Ê®°Êì¨ API Âª∂ÈÅ≤
 */
export async function simulateApiDelay(delay: number): Promise<void> {
    // ÈñãÁôºÁí∞Â¢ÉÊâçÊ®°Êì¨Âª∂ÈÅ≤
    if (process.env.NODE_ENV === 'development') {
        await new Promise(resolve => setTimeout(resolve, delay));
    }
}

/**
 * Âª∫Á´ãÂø´ÂèñÊ®ôÈ†≠
 */
export function createCacheHeaders(ttl: number = CACHE_CONFIG.DEFAULT_TTL): Record<string, string> {
    return {
        'Cache-Control': `public, max-age=${ttl}, s-maxage=${ttl}`,
        'Vary': 'Accept-Language, Accept-Encoding',
    };
}

/**
 * Ê∑±Â∫¶Ë§áË£ΩÁâ©‰ª∂
 */
export function deepClone<T>(obj: T): T {
    if (obj === null || typeof obj !== 'object') {
        return obj;
    }

    if (obj instanceof Date) {
        return new Date(obj.getTime()) as unknown as T;
    }

    if (Array.isArray(obj)) {
        return obj.map(item => deepClone(item)) as unknown as T;
    }

    const cloned = {} as T;
    for (const key in obj) {
        if (obj.hasOwnProperty(key)) {
            cloned[key] = deepClone(obj[key]);
        }
    }

    return cloned;
}

/**
 * Ê™¢Êü•ÊòØÂê¶ÊáâË©≤Ê®°Êì¨ÈåØË™§
 */
export function shouldSimulateError(): boolean {
    // Áõ¥Êé•‰ΩøÁî® env ‰∏≠ÁöÑÈåØË™§Ê®°Êì¨Ë®≠ÂÆö
    if (!env.MOCK_ERROR_ENABLED) {
        return false;
    }
    return Math.random() < env.MOCK_ERROR_RATE;
}

/**
 * Âª∫Á´ãÈåØË™§ÂõûÊáâ
 */
export function createErrorResponse(status: number = 500, message: string = 'Internal Server Error') {
    return Response.json(
        {
            success: false,
            error: {
                code: status,
                message,
                timestamp: new Date().toISOString(),
            }
        },
        {
            status,
            headers: {
                'Cache-Control': 'no-cache, no-store, must-revalidate',
            }
        }
    );
}