import { env } from '@/env.mjs';

/**
 * ÊúçÂãôÁ´ØË™ûÁ≥ªÈÖçÁΩÆ
 * Âè™ËÉΩÂú®ÊúçÂãôÁ´Ø‰ΩøÁî®
 */
const SERVER_CONFIG = {
    SUPPORTED_LOCALES: env.SUPPORTED_LOCALES.split(',').map((l) => l.trim()),
    DEFAULT_LOCALE: env.DEFAULT_LANGUAGE,
    LOCALE_PREFIX_MODE: env.LOCALE_PREFIX_MODE,
    FALLBACK_LOCALE: 'zh-TW' as const,

    DETECTION: {
        ENABLED: env.MULTI_LANGUAGE_ENABLED,
        GEO_ENABLED: env.GEO_DETECTION_ENABLED,
        CACHE_TTL: 3600,
    },

    CACHE: {
        STRATEGY: env.I18N_CACHE_STRATEGY,
        TTL: 3600,
    },

    COUNTRY_SUBDOMAIN_MAP: JSON.parse(env.COUNTRY_SUBDOMAIN_MAP),
} as const;

/**
 * ÂÆ¢Êà∂Á´ØË™ûÁ≥ªÈÖçÁΩÆ
 * ÂèØ‰ª•Âú®ÂÆ¢Êà∂Á´ØÂíåÊúçÂãôÁ´Ø‰ΩøÁî®
 */
const CLIENT_CONFIG = {
    SUPPORTED_LOCALES: env.NEXT_PUBLIC_SUPPORTED_LOCALES.split(',').map((l) => l.trim()),
    DEFAULT_LOCALE: env.NEXT_PUBLIC_DEFAULT_LOCALE,
    LOCALE_PREFIX_MODE: env.NEXT_PUBLIC_LOCALE_PREFIX_MODE,
    MULTI_LANGUAGE_ENABLED: env.NEXT_PUBLIC_MULTI_LANGUAGE_ENABLED,

    DETECTION: {
        STRATEGY: env.NEXT_PUBLIC_GEO_DETECTION_STRATEGY,
        REDIRECT_MODE: env.NEXT_PUBLIC_GEO_REDIRECT_MODE,
        CDN_HEADER: env.NEXT_PUBLIC_CDN_COUNTRY_HEADER,
    },
} as const;

/**
 * Áµ±‰∏ÄË™ûÁ≥ªÈÖçÁΩÆ‰ªãÈù¢
 * Êèê‰æõÁí∞Â¢ÉÁÑ°ÈóúÁöÑÈÖçÁΩÆË®™ÂïèÊñπÂºè
 */
export const LOCALE_CONFIG = {
    // Ê†πÊìöÂü∑Ë°åÁí∞Â¢ÉÈÅ∏ÊìáÈÖçÁΩÆÊ∫ê
    get SUPPORTED_LOCALES() {
        return typeof window === 'undefined'
            ? SERVER_CONFIG.SUPPORTED_LOCALES
            : CLIENT_CONFIG.SUPPORTED_LOCALES;
    },

    get DEFAULT_LOCALE() {
        return typeof window === 'undefined'
            ? SERVER_CONFIG.DEFAULT_LOCALE
            : CLIENT_CONFIG.DEFAULT_LOCALE;
    },

    get LOCALE_PREFIX_MODE() {
        return typeof window === 'undefined'
            ? SERVER_CONFIG.LOCALE_PREFIX_MODE
            : CLIENT_CONFIG.LOCALE_PREFIX_MODE;
    },

    get DETECTION() {
        if (typeof window === 'undefined') {
            return {
                ENABLED: SERVER_CONFIG.DETECTION.ENABLED,
                GEO_ENABLED: SERVER_CONFIG.DETECTION.GEO_ENABLED,
                CACHE_TTL: SERVER_CONFIG.DETECTION.CACHE_TTL,
                // ÂÆ¢Êà∂Á´ØÂ∞àÁî®Â±¨ÊÄßÂú®ÊúçÂãôÁ´ØËøîÂõû undefined
                STRATEGY: undefined,
                REDIRECT_MODE: undefined,
                CDN_HEADER: undefined,
            };
        } else {
            return {
                ENABLED: true, // ÂÆ¢Êà∂Á´ØÂÅáË®≠ÂïüÁî®
                STRATEGY: CLIENT_CONFIG.DETECTION.STRATEGY,
                REDIRECT_MODE: CLIENT_CONFIG.DETECTION.REDIRECT_MODE,
                CDN_HEADER: CLIENT_CONFIG.DETECTION.CDN_HEADER,
                // ÊúçÂãôÁ´ØÂ∞àÁî®Â±¨ÊÄßÂú®ÂÆ¢Êà∂Á´ØËøîÂõû undefined
                GEO_ENABLED: undefined,
                CACHE_TTL: undefined,
            };
        }
    },

    // Â∑•ÂÖ∑ÂáΩÊï∏
    isValidLocale: (locale: string): boolean => {
        const locales = typeof window === 'undefined'
            ? SERVER_CONFIG.SUPPORTED_LOCALES
            : CLIENT_CONFIG.SUPPORTED_LOCALES;
        return locales.includes(locale);
    },

    // ÊúçÂãôÁ´ØÂ∞àÁî®ÂáΩÊï∏
    getSubdomainByCountry: (country: string): string | null => {
        if (typeof window !== 'undefined') {
            console.warn('getSubdomainByCountry should only be called on server side');
            return null;
        }
        return SERVER_CONFIG.COUNTRY_SUBDOMAIN_MAP[country] || null;
    }
} as const;

// Áõ¥Êé•Â∞éÂá∫ÊúçÂãôÁ´ØÂíåÂÆ¢Êà∂Á´ØÈÖçÁΩÆÔºà‰æõÁâπÊÆäÈúÄÊ±Ç‰ΩøÁî®Ôºâ
export const SERVER_LOCALE_CONFIG = SERVER_CONFIG;
export const CLIENT_LOCALE_CONFIG = CLIENT_CONFIG;

export type SupportedLocale = string;